@import "../../resources/styles/index.scss";

.Starlink {
  height: 95vh;
}

.StarlinkInfo {
  z-index: 900;
  position: absolute;
  top: 10%;
  left: 2%;
  @include flex-column;
  max-width: 400px;

  h2 {
    font-size: 3rem;
    color: $fontPrimary;
    margin-bottom: 1rem;
  }

  h3 {
    font-size: 1.2rem;
    color: $fontSecondary;
  }

  h2,
  h3,
  h4 {
    font-weight: 300;
  }

  h4 {
    margin-top: 1rem;
    color: $fontSecondary;
  }
}

a {
  text-decoration: none;
  color: $blue;
}
